.gen_dp {
    width: 180px;
    padding: 10px 20px;
    border: 2px solid #212121b9;
    background-color: #fff;
    color: #000000;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 14px;
    margin: 4px 2px;
    transition: background-color 1s, color 2s, border-color 2s, border 0.33s,
        font-weight 0.5s;
    cursor: pointer;
    border-radius: 50px;
    margin-bottom: 20px;
}
